html,
.root {
  background-color: rgb(231, 231, 231);
  margin: 0;
}

.App {
  width: 1150px;
  height: 855px;
  background-color: white;
  border-radius: 3mm;
  box-shadow: 0 1px 5px 0.3px rgb(117, 117, 117);
  overflow: hidden;
}

/* Media Query */
@media only screen and (max-width: 1150px) {
  .App {
    margin: auto;
    height: 100vh;
    width: 100vw;
  }
}
