@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;750&display=swap);
html,
.root {
  background-color: rgb(231, 231, 231);
  margin: 0;
}

.App {
  width: 1150px;
  height: 855px;
  background-color: white;
  border-radius: 3mm;
  box-shadow: 0 1px 5px 0.3px rgb(117, 117, 117);
  overflow: hidden;
}

/* Media Query */
@media only screen and (max-width: 1150px) {
  .App {
    margin: auto;
    height: 100vh;
    width: 100vw;
  }
}

.fieldbox {
  position: relative;
  width: 100%;
  padding-left: 1%;
  margin-top: 1%;
}

.tagfieldbox {
  position: relative;
  width: 100%;
  padding-left: 1%;
  margin-top: 2%;
}

.field {
  font-size: 28px;
  font-family: "Raleway", sans-serif;
  border: none;
  outline: 0;
  border-bottom: 2px solid lightgray;
  width: 99.2%;
  height: 5vh;
}

.field:hover,
.field:focus {
  border: none;
  border-bottom: 2px solid black;
}

@media screen and (min-width: 280px) and (max-width: 1149px) {
  .field {
    font-size: 22px;
  }
}

@media screen and (min-width: 480px) and (max-width: 1149px) {
  .fieldbox {
    width: 100%;
  }
  .tagfieldbox {
    width: 100%;
  }
  .field {
    font-size: 22px;
    padding: 4% 0 4% 0;
  }
}

/* Card Container */
.card {
  display: flex;
  flex-flow: row wrap;
  border-bottom: 1px solid lightgray;
  justify-content: space-between;
}

/* Image Container */
.image-container {
  margin: 35px 0 0 24px;
  width: 10%;
  height: 100%;
}

/* Image */
.circle {
  width: 190px;
  height: 190px;
  border: 1px solid rgb(163, 156, 156);
  border-radius: 50%;
  overflow: hidden;
  background-color: white;
}

img {
  width: 100%;
}

/* Student Container */
.student-container {
  display: flex;
  flex-flow: column wrap;
  align-items: flex-start;
  margin: 1% 0 1% 0;
  width: 65%;
}

/* Button Container */
.button-container {
  margin-top: 29.5px;
}

/* Student Name */
.fullname {
  text-transform: uppercase;
  font-weight: bolder;
  font-size: 52px;
  margin: 1% 0 0 0;
}

/* Button */
.btn {
  font-size: 45px;
  display: flex;
  background: transparent;
  justify-content: center;
  align-items: center;
  align-content: center;
  border: none;
  color: rgb(160, 158, 158);
  cursor: pointer;
  width: 9%;
  height: 4vh;
}

.btn:hover {
  color: black;
}

/* Profile Container */
.profile {
  margin: 0px 0px 0 30px;
  line-height: 10px;
  font-size: 20px;
}

/* Test Container */
.test-container-collapsed {
  display: none;
  width: 47%;
}

.test-container-expanded {
  width: 47%;
}

ul {
  padding: 0;
}

.test-container {
  display: flex;
  flex-flow: row wrap;
  margin-left: 30px;
  font-size: 20px;
}

.testNumber {
  margin: 0;
  padding: 0;
  margin-right: 7%;
}

.testValue {
  margin: 0;
  padding: 0;
}

/* Media Query */
@media screen and (min-width: 280px) and (max-width: 1149px) {
  .card {
    flex-flow: row wrap;
    align-items: center;
    justify-content: space-between;
    margin-top: 2%;
  }
  .image-container {
    margin: 0 0 5% 0;
    width: 100%;
    display: flex;
    justify-content: center;
    order: 2;
  }
  .student-container {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    width: 100%;
    order: 3;
  }
  .button-container {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    position: static;
    margin: 0;
    order: 1;
  }
  .fullname {
    font-size: 22px;
  }
  .profile {
    font-size: 14px;
    margin: 0;
  }
  .test-container-plus {
    display: none;
    width: 98px;
    height: 136px;
    margin-bottom: 3%;
  }
  .test-container-minus {
    width: 98px;
    height: 136px;
    margin-bottom: 3%;
  }
  .test-container {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    font-size: 14px;
    margin: 0;
    padding: 0;
    width: 98px;
  }
  .btn {
    font-size: 22px;
  }
}

@media screen and (min-width: 564px) and (max-width: 1149px) {
  .fullname {
    font-size: 45px;
  }
  .profile {
    font-size: 20px;
  }
  .test-container-plus {
    width: 115px;
    margin-bottom: 10%;
  }
  .test-container-minus {
    width: 115px;
    margin-bottom: 10%;
  }
  .test-container {
    font-size: 20px;
    width: 115px;
  }
  .btn {
    font-size: 45px;
  }
}

@media screen and (min-width: 804px) and (max-width: 1149px) {
  .fullname {
    font-size: 52px;
  }
  .test {
    margin-left: 40%;
  }
  .btn {
    font-size: 52px;
  }
}

.tag-container {
  position: relative;
  width: 35%;
  margin: 0 0 3% 0;
}

.unordered {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  list-style-type: none;
  margin-left: 1%;
  padding: 0 0 0 27px;
  width: 100%;
}

.list {
  font-size: 20px;
  background: lightgray;
  border-radius: 1.5mm;
  padding: 5%;
  margin: 1.5%;
}

.tagInput {
  font-size: 20px;
  font-family: "Raleway", sans-serif;
  border: none;
  outline: 0;
  border-bottom: 2px solid lightgray;
  padding: 0px 0 5% 6%;
  margin-left: 20px;
}

.tagInput:hover,
.tagInput:focus {
  border: none;
  border-bottom: 2px solid black;
}

/* Media Query */
@media screen and (min-width: 280px) and (max-width: 1149px) {
  .tag-container {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    width: 100%;
    margin: 0;
    padding: 0;
  }
  .unordered {
    display: flex;
    justify-content: center;
    width: 50%;
    margin: 0;
    padding: 0;
  }

  .tagInput {
    font-size: 14px;
    padding: 0;
    align-items: center;
    margin: 2% 0 2% 0;
  }
  .list {
    margin: 1;
    font-size: 14px;
    padding: 2%;
  }
}

@media screen and (min-width: 564px) and (max-width: 1149px) {
  .tagInput,
  .list {
    font-size: 20px;
  }
  .unordered {
    width: 40%;
  }
}

@media screen and (min-width: 1206px) and (max-width: 1149px) {
  .tagInput,
  .list {
    font-size: 20px;
  }
  .unordered {
    width: 50%;
  }
}

/* Student Container */
.container {
  width: 100%;
  height: 83.6%;
  padding: 0;
  overflow: hidden;
  overflow-y: scroll;
  margin: 0;
}

/* Scrollbar */
::-webkit-scrollbar-track {
  background: none;
}

::-webkit-scrollbar {
  width: 8px;
  height: 2px;
}
::-webkit-scrollbar-thumb {
  background: rgb(143, 138, 138);
  border-radius: 10px;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Raleway", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  height: 100vh;
}

